"use server";
import { z } from "zod";

export async function processReceipt(fd: FormData) {
  const file = fd.get("file") as File;
  if (!file) return { ok: false, error: "No file" };

  // Convert → buffer → base64
  const ab   = await file.arrayBuffer();
  const buf  = Buffer.from(ab);

  // Resize if > 1 MB (keeps quality, drops size)
  const sharp = (await import("sharp")).default;
  let out = buf;
  if (buf.length > 1_000_000) {
    out = await sharp(buf)
               .resize({ width: 1200, withoutEnlargement: true })
               .jpeg({ quality: 85 })
               .toBuffer();
  }

  const base64 = `data:image/jpeg;base64,${out.toString("base64")}`;

  // Call Gemini 1.5 Flash (free tier)
  const { GoogleGenerativeAI } = await import("@google/generative-ai");
  const genAI = new GoogleGenerativeAI(process.env.GOOGLE_API_KEY!);
  const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

  const prompt = `Extract JSON: {merchant,total,tax,date,category,emoji} from this receipt.`;
  const result = await model.generateContent([prompt, { inlineData: { data: base64.split(",")[1], mimeType: "image/jpeg" } }]);
  const txt = result.response.text();

  try {
    const data = JSON.parse(txt.replace(/```json|```/g, ""));
    return { ok: true, data };
  } catch {
    return { ok: false, error: "Parse failed" };
  }
}
